*,
*:before,
*:after {
  box-sizing: border-box;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.page {
  height: 100%;
}

.page-body {
  display: flex;
  flex-direction: column;
  min-height: 100%;
  font-family: "Blogger Sans", "Arial", sans-serif;
  font-size: 16px;
  line-height: 1.375rem;
  font-weight: 700;
  color: #ffffff;
  margin: 0;
  padding: 0;
  background-color: #161c35;
}

.main-page {
  flex: 1 1 auto;
}

.wrapper {
  width: 320px;
  margin: 0 auto;
  padding: 0 25px;
}
@media (min-width: 768px) {
  .wrapper {
    width: 768px;
    padding: 0 40px;
  }
}
@media (min-width: 1440px) {
  .wrapper {
    width: 1440px;
    padding: 0 60px;
  }
}

.nav__list,
.contacts,
.social,
ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

p {
  margin: 0;
}

.nav__link,
a {
  display: block;
  color: inherit;
  text-decoration: none;
}

.button {
  cursor: pointer;
  font: inherit;
  text-align: center;
  vertical-align: middle;
  border: none;
}

.more-button {
  position: relative;
  font-size: 18px;
  line-height: 18px;
  color: #1d2e5b;
  background: #ffd74b;
  border-radius: 10px;
  padding: 22px 142px 22px 23px;
}
@media (min-width: 768px) {
  .more-button {
    font-size: 24px;
    line-height: 24px;
    border-radius: 20px;
    padding: 31px 32px 27px 33px;
    margin-left: 35px;
    margin-right: 35px;
  }
}
@media (min-width: 1440px) {
  .more-button {
    border-radius: 20px;
    display: inline-block;
  }
}
.more-button::after {
  content: "";
  position: absolute;
  width: 0;
  height: 0;
  border: 0 solid transparent;
  border-bottom-width: 6px;
  border-top-width: 6px;
  border-left: 10px solid #1d2e5b;
  right: 35px;
  bottom: 27px;
}
@media (min-width: 768px) {
  .more-button::after {
    bottom: 33px;
  }
}

.nav--closed .nav__list {
  display: none;
}
@media (min-width: 1440px) {
  .nav--closed .nav__list {
    display: flex;
  }
}

.nav--closed .authorization-button {
  display: none;
}

.nav--closed .contacts {
  display: none;
}

.nav--closed .social {
  display: none;
  /*  @media (min-width: $desktop-width) {
    display: block;
  } */
}

.nav--opened .burger::before {
  bottom: 4px;
  transform: rotate(-45deg);
}

.nav--opened .burger::after {
  top: 4px;
  transform: rotate(45deg);
}

